import React, {Component} from "react"

class Comp extends Component {

    constructor() {
        super()
        this.state = {
            loging: false
        }
        this.handleClick = this.handleClick.bind(this)
    }

    handleClick() {
        console.log("I was clicked button")
        this.setState(prevState => {
            return {
                loging: !prevState.loging
            }
        })
    }

    render() {
        
        let loginText = this.state.loging ? "Am logined" : "Sory, but am do not logined"
        let buttonText = this.state.loging ? "Close":"Login"

        return (
            <div>
                <p>
                    {loginText}
                </p>
                <button onClick={this.handleClick}>{buttonText}</button>
            </div>
        )
    }
}

export default Comp