import React from "react";
import ReactDom from "react-dom";
import logo from './logo.png';

function HeaderCenter() {
    
    const styleD = {
        "float": "left", 
        "width": "30%"
    };

    const styleI = {
        "width": "150px",
        "height": "150px"
    };
    
    return(
        <div style={styleD}>
            <img src={logo} style={styleI}></img>
        </div>
    );
}

export default HeaderCenter;