import React, { Component } from 'react';
import logo from './logo.svg';
import './App.css';
import Header from "./components/header/Header";
import MainConteiner from "./components/MainConteiner";
import Footer from "./components/Footer";
import Product from "./components/joke/Joke"
import productsData from "./components/joke/jokesData"

class App extends Component {
  
  constructor() {
    super()
    this.state = {
        todos: productsData
    }
    this.handleChange = this.handleChange.bind(this)
}

  handleChange(id) {
    this.setState(prevState => {
        
        const updatedTodos = prevState.todos.map(todo => {
            
            if (todo.id === id) {
                todo.completed = !todo.completed
            }
            
            return todo
        })
        
        return {
            todos: updatedTodos
        }
    })
  }

  render() {
    
    const style = {
      "display": "flex", 
      "flexDirection": "column"
    }

    const jokeComponents = this.state.todos.map(item => <Product key={item.id} item={item} handleChange={this.handleChange}/>)
   
    return (
      <div className="App">
        <Header />
        <div style={this.style}>
          {jokeComponents}
        </div>
        <Footer />

      </div>
    );
  }
}

export default App