import React from "react"

function Product(props) {
    
    const style = {
        "margin": "auto",
        "border": "1px solid gray",
        "flexBasis": "auto",
        "width": "200",
        "padding": "10"
    };
    
    return (
        <div className="todo-item" style={style}>
            <input type="checkbox" 
                checked={props.item.completed} 
                onChange={() => props.handleChange(props.item.id)}/>
            <p>{props.item.text}</p>
        </div>
    )
}

export default Product

