import React from "react";
import ReactDom from "react-dom";

function HeaderInfo() {
    
    const style = {
        "float": "left", 
        "margin": "1% 0 0 1%",
        "height": 150
    };
    
    return(
        <div id="info" style={style}>
            <a name="top"></a>
            <h3>Контакты</h3>
            <p>г. Днепр, Украина</p>
            <p>тел. +38(097) 555 55 55</p>
            <p>e-mail: xxxxxx@xxxx.xxx</p>
        </div>
    );
}

export default HeaderInfo;